import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/color/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

// const Panel = Collapse.Panel;
// const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class Color extends Component {
  state = {
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getColors } = this.props;
    getColors();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.color.error) {
      notification("error", this.props.color.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.color.success) {
      notification("success", this.props.color.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getColors } = this.props;
    getColors(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getColors } = this.props;
    this.setState({ search: null });
    getColors();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateColor(id);
    } else {
      this.props.deactivateColor(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getColors } = this.props;
    const { search } = this.props.color;
    const { filter } = this.props.color;
    getColors(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_color",
    rowKey: "id_color",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="colors.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="colors.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "color_name",
        rowKey: "color_name",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "label.description" }),
        dataIndex: "description",
        rowKey: "description",
        width: "65%",
        render: text => {
          if (text && text.length > 75) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 75) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // console.log(this.props.color);
    // Data setup
    const { colors } = this.props.color;
    // console.log(colors); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Color") && columns.length === 3) {
      columns.push(this.listAction);
    }

    Object.keys(colors).map((color, index) => {
      return dataSource.push({
        ...colors[color],
        key: color
      });
    });

    // Pagination Setup
    const { current_page, page_color, total } = this.props.color;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="colors.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Color") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="colors.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "name" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {colors.length === 0 && !this.props.color.loading ? (
              <Empty description={<IntlMessages id="colors.table.noData" />} />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.color.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_color mengunakan pagination API
                    // jika tidak ada page_color menggunakan pagination bawaan
                    pagination={
                      page_color
                        ? {
                            current: current_page,
                            pageColor: parseInt(page_color),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    color: state.color,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(Color);
